.App {
    text-align: center;
    background-color: #282c34;
    color: white;
    height: 100vh;
}

.header {
    height: 150px;
    font-size: 30px;
    padding: 20px;
}

.info {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-around;
}

.game {
    max-width: 460px;
    margin: 0 auto;
}

.cards {
    margin: 0 auto;
    padding-bottom: 100px;

    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
}

.popup-content {
    background: #2b9595;
    border: 0;
    border-radius: 5px;
    color: white;
    max-width: 250px;
    text-align: center;
    padding: 20px;
}

.popup-content .close {
    cursor: pointer;
    position: absolute;
    right: 5px;
    top: -5px;
    font-size: 24px;
}